.grid-container {
  display: grid;
  grid-template-columns: 200px 600px;
  grid-template-rows: 100px 300px 300px;
  gap: 10px 20px;
  grid-template-areas:
    "navigator header"
    "navigator chart"
    "navigator map";
}

.header {
  grid-area: header;
  background-color: #bddbfa;
}

.navigator {
  grid-area: navigator;
  background-color: #84bbf3;
}

.header {
  grid-area: header;
}

.chart {
  grid-area: chart;
  border: 3px solid steelblue;
  border-radius: 10px;
  padding-bottom: 40px;
}

.map {
  grid-area: map;
  border: 3px solid steelblue;
  border-radius: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
  padding-right: 5px;
}

.button {
  backface-visibility: hidden;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  background: #58c;
  border-radius: 0px;
  border: 0px solid #246;
  border-width: 0px 0px 4px 0px;
  padding: 10px 20px 10px 20px;
  color: #fff;
  font-size: initial;
  font-family: Helvetica Neue;
  font-weight: 900;
  font-style: normal;
  margin-left: 10%;
  margin-right: 10%;
  min-width: 80%;
  margin-top: 5px;
  margin-bottom: 5px;
}
.button:hover {
  color: black;
}

.myButton {
  box-shadow: inset 0px 1px 0px 0px #dcecfb;
  background: linear-gradient(to bottom, #bddbfa 5%, #80b5ea 100%);
  background-color: #bddbfa;
  border-radius: 6px;
  border: 1px solid #84bbf3;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 15px;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #528ecc;
  width: 40%;
  margin-top: 5px;
  margin-left: 5%;
  margin-right: 5%;
}
.myButton:hover {
  background: linear-gradient(to bottom, #80b5ea 5%, #bddbfa 100%);
  background-color: #80b5ea;
}
.myButton:active {
  position: relative;
  top: 1px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
